import React, { useEffect, useMemo } from 'react'
import { useFormikContext } from 'formik';
import { graphql, navigate, withPrefix } from 'gatsby';
import { max } from 'lodash';
import { getImage, StaticImage } from 'gatsby-plugin-image';
import { useClipboard, useToast, Icon, HStack, Image } from "@chakra-ui/react"
import { BgImage } from "gbimage-bridge"
import styled from '@emotion/styled'
import { MdCheckCircle, MdClose } from 'react-icons/md'
import { useLocalStorage } from 'react-use';

import Box from '../components/Box';
import Flex from '../components/Flex';
import Text from '../components/Text';
import Button from '../components/Button';
import Container from '../components/Container';
import BackgroundImage from '../components/BackgroundImage';
import theme, { responsive } from '../components/ThemeProvider/theme';

import Layout from '../containers/Layout'
import resultData from '../json/result.json'
import { trackClick } from '../utils/trackEvent';

const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const resultInfos = [
  {
    title: '',
    desc: ' GoShare ',
    linkText: '',
    href: 'https://goshare.re/TravelQuizFB',
  },
  {
    title: '',
    desc: ' GoShare App ',
    city: '',
    cityColor: 'custom.blue',
    text: '',
    // icon: <StaticImage src="../image/lollipop.png" layout="fullWidth" alt="" />,
    icon: <Image alt="" src={require('../image/lollipop.png').default} />,
    linkText: ' App',
    href: 'https://goshare.re/TravelQuizApp',
  },
  {
    title: '',
    desc: '9.10  GoShare\n',
    descAilgn: 'center',
    city: true,
    cityColor: 'custom.pink',
    cityFontSize: '1.125rem',
    textColor: 'custom.pink',
    textAlign: 'center',
    mt: '1.875em',
    text: '',
  },
]

const Result = ({
  pageContext: { id, questions, data },
  data: { site: { siteMetadata: { url } }, mobile }
}) => {
  const { values } = useFormikContext()
  const percentCalc = useMemo(() => {
    const p = (values.points * 100) / data[id].reduce((res, scores) => {
      res = res + max(scores)
      return res
    }, 0)
    return Math.round(p)
  }, [data, id, values.points])
  const [percent] = useLocalStorage(`GoShareOMG:${id}`, percentCalc)
  useEffect(() => {
    if (!percent) {
      navigate('/')
    }
  }, [percent])
  const imageMobile = getImage(mobile)
  const { onCopy } = useClipboard(url)
  const toast = useToast()
  if (!percent) return null
  const res = resultData[id]
  return (
    <Layout
      headerColor={res.bg !== 'custom.lightYellow' && "white"}
      headerProps={{ justifyContent: 'center' }}
      footerProps={{ bg: 'custom.blue', pb: '3.75em', color: 'white', maxWidth: responsive('auto', '23.4375rem'), mx: "auto"}}
      title={res.personality}
      noPt
    >
      <Box.Relative maxWidth={responsive('auto', '23.4375rem')} mx="auto" overflow="hidden">
        <Box.Absolute widht="86%" top="-2em" transform="translateX(-60%)">
          <StaticImage src="../image/pink_ball.png" alt="" />
        </Box.Absolute>
        <Box.Absolute widht="50%" top="0" right="0" transform="translate(50%, -50%)">
          <StaticImage src="../image/blue_ball.png" alt="" />
        </Box.Absolute>
        <Box pt={theme.headerHeight} bg={res.bg}>
          <Container pt="4em">
            <Box textAlign="center">
              <Text fontWeight="bold" color="custom.darkBlue" textAlign="center" fontSize="1.75rem">
                
                <Text.Inline fontWeight="900" fontSize="2.5rem"> {percent}% </Text.Inline>
                
              </Text>
              <Text
                lineHeight="1"
                fontWeight="bold"
                textAlign="center"
                color="custom.darkBlue"
                fontSize="2.625rem"
              >{res.personality}</Text>
              <Box.Relative maxWidth="315px" mx="auto">
                <Box.Relative mt="1.875em" width="59.7%" maxWidth="11.75em" height="15em" mx="auto">
                  <GatsbyBgImage style={{ padding: '3px' }} image={imageMobile} />
                  <Box.FullAbs border="6px solid" borderColor="custom.pink" />
                </Box.Relative>
                <Box.FullAbs left="-1.875em" right="-1.875em">
                  <BackgroundImage src={require('../image/deco.png').default} ratio={770 / 504} />
                  {/* <StaticImage src="../image/deco.png" alt="" /> */}
                </Box.FullAbs>
              </Box.Relative>
              <Text my="1.875rem" fontWeight="bold" fontSize="1.375rem" color="custom.blue">
                 
                <Text.Inline color="custom.darkBlue"> {res.city}</Text.Inline>
              </Text>
              <Box.Relative maxWidth="21em" mx="auto">
                <Box.Relative
                  borderRadius="1.5rem"
                  p="1.875rem"
                  bg={res.boardBg}
                  color={res.boardBg === 'custom.teal' ? 'custom.darkBlue' : 'white'}
                  mx="auto"
                  overflow="hidden"
                  transform="translateZ(0)"
                >
                  <Box pb="0.9375rem" mb="0.9375rem" borderBottom="1px solid">
                    <Text fontWeight="bold" whiteSpace="pre-wrap">{res.slogan}</Text>
                  </Box>
                  <Text textAlign="justify" fontWeight="bold" fontSize="0.75rem">{res.desc}</Text>
                  <Box.Relative mt="1.875em" width="67.5%" ml="25%">
                    <Image src={require('../image/dialog.png').default} alt="" />
                    <Box.Absolute fontWeight="bold" top="14.5%" left="0" right="0" color="custom.blue" textAlign="center">
                      <Text fontSize="0.75rem"></Text>
                      <Text fontSize="1.125rem">{res.unsuitable}</Text>
                    </Box.Absolute>
                  </Box.Relative>
                  <Box.Relative width="63.5%" ml="38%">
                    <Image src={res.boardBg === 'custom.blue' ? require('../image/dialog_pink.png').default : require('../image/dialog_blue.png').default} alt="" />
                    {/* {res.boardBg === 'custom.blue' ? (
                      <StaticImage src="../image/dialog_pink.png" layout="fullWidth" />
                    ) : (
                      <StaticImage src="../image/dialog_blue.png" layout="fullWidth" />
                    )} */}
                    <Box.Absolute top="50%" transform="translateY(-50%)" left="0" right="0" color="white" textAlign="center">
                      <Text
                        ml="10%"
                        fontWeight="bold"
                        whiteSpace="pre-wrap"
                        fontSize="0.75em"
                      >{res.reason}</Text>
                    </Box.Absolute>
                  </Box.Relative>
                  <Box.Absolute bottom="-21px" left="0" width="6em">
                    <Image alt="" src={require('../image/power.png').default} />
                    {/* <StaticImage src="../image/power.png" alt="" layout="fullWidth" /> */}
                  </Box.Absolute>
                </Box.Relative>
                <Box.Absolute bottom="-20px" left="0" width="6em">
                  <Image alt="" src={require('../image/power-legs.png').default} />
                  {/* <StaticImage src="../image/power-legs.png" alt="" layout="fullWidth" /> */}
                </Box.Absolute>
              </Box.Relative>
            </Box>
            <Flex py="3.75rem" alignItems="center" justifyContent="center" flexDirection="column">
              <Box width="180px">
                <Button
                  as="a"
                  width="100%"
                  height="50px"
                  href={withPrefix(`/destination/${id}/${percent}/GoShare_OMG .jpg`)}
                  // download={`Goshare-OMG.jpg`}
                  bg={res.buttonColor}
                  color={res.boardBg === 'custom.teal' ? 'white' : 'custom.darkBlue'}
                  _hover={{
                    bg: res.buttonColor,
                    color: res.boardBg === 'custom.teal' ? 'white' : 'custom.darkBlue'
                  }}
                  _active={{
                    bg: res.buttonColor,
                    color: res.boardBg === 'custom.teal' ? 'white' : 'custom.darkBlue'
                  }}
                  fontSize="1.125rem"
                  onClick={() => trackClick('')}
                >
                  
                </Button>
              </Box>
              <Box mt="0.9375rem" width="180px">
                <Button
                  width="100%"
                  height="50px"
                  onClick={() => {
                    onCopy()
                    trackClick('')
                    toast({
                      duration: 3000,
                      isClosable: true,
                      render: ({ onClose, ...p }) => (
                        <HStack bg="custom.gray" rounded="xl" color="#7C8C8F" p="1em" {...p}>
                          <Icon w="1.125em" h="1.125em" as={MdCheckCircle} />
                          <Text></Text>
                          <Box flex="1" />
                          <Icon onClick={onClose} as={MdClose} />
                        </HStack>
                      ),
                    })
                  }}
                  bg={res.buttonColor}
                  color={res.boardBg === 'custom.teal' ? 'white' : 'custom.darkBlue'}
                  _hover={{
                    bg: res.buttonColor,
                    color: res.boardBg === 'custom.teal' ? 'white' : 'custom.darkBlue'
                  }}
                  _active={{
                    bg: res.buttonColor,
                    color: res.boardBg === 'custom.teal' ? 'white' : 'custom.darkBlue'
                  }}
                  fontSize="1.125rem"
                ></Button>
              </Box>
            </Flex>
          </Container>
        </Box>
        <Box bg="custom.blue">
          <Container
            py="3.75rem"
            px="2em"
          >
            {resultInfos.map((d, i) => (
              <Box.Relative
                width="100%"
                maxWidth="21em"
                minHeight="15em"
                mx="auto"
                borderRadius="1.5rem"
                bg="white"
                py="1.875em"
                px="3.125em"
                mt={i && '3.75rem'}
              >
                <Box.Relative textAlign="center">
                  <Box.Absolute opacity="0.5" borderRadius="full" left="0em" right="-0em" bottom="0" height="1em" bg="custom.teal" />
                  <Flex display="inline-flex" position="relative" alignItems="center" fontWeight="bold" color="custom.blue">
                    <Box.Relative fontSize="1.875rem">{i + 1}</Box.Relative>
                    <Box.Relative
                      mt="0.25em"
                      ml="0.25em"
                      fontSize="1.375rem"
                    >{d.title}</Box.Relative>
                  </Flex>
                </Box.Relative>
                <Box.Relative
                  mt={d.mt || "1.25em"}
                  fontSize="0.875em"
                  fontWeight="bold"
                  color="custom.darkBlue"
                >
                  <Text textAlign={d.descAilgn} lineHeight="2" whiteSpace="pre-wrap">{d.desc}</Text>
                  {d.city && (
                    <Text textAlign={d.textAlign} lineHeight="2">
                      {d.city}
                      <Text.Inline color={d.cityColor} fontSize={d.cityFontSize}>
                        {res.city}
                      </Text.Inline>
                    </Text>
                  )}
                  <Text textAlign={d.textAlign} color={d.textColor} fontSize={d.cityFontSize}>{d.text}</Text>
                  <Box.Absolute left="75%" top="40%" width="5.25em">
                    {d.icon}
                  </Box.Absolute>
                </Box.Relative>
                {d.linkText && (
                  <Box.Absolute width="180px" bottom="0" left="50%" transform="translate(-50%, 50%)">
                    <Button.Pink
                      width="100%"
                      href={d.href}
                      onClick={() => trackClick(d.linkText)}
                      fontSize="1.125rem"
                      height="50px"
                    >
                      {d.linkText}
                    </Button.Pink>
                  </Box.Absolute>
                )}
              </Box.Relative>
            ))}
          </Container>
        </Box>
      </Box.Relative>
    </Layout>
  )
}

export default Result

export const query = graphql`
query resultQuery($image: String)  {
  site {
    siteMetadata {
      url
    }
  }
  mobile: file(relativePath: { eq: $image }) {
    childImageSharp {
      gatsbyImageData(
        placeholder: BLURRED
        width: 768
      )
    }
  }
  tablet: file(relativePath: { eq: $image }) {
    childImageSharp {
      gatsbyImageData(
        placeholder: BLURRED
        width: 1920
      )
    }
  }
}
`
